@navy-text-color: #FFF;
@navy-background-color: #143b69;
@navy-border-color: #000;

@grey-text-color: #000810;
@grey-background-color: #d3d3d3;
@grey-border-color: #143b69;

.fm-title {
  width: 100%;
  padding: 15px 10px 20px 10px;
  color: @text-color;
  background-color: @title-background-color;
}

.fm-title__header {
  display: inline;
  width: 30%;
}

.fm-title__content {
  display: inline;
  width: 70%;
  float: right;
  text-align: right;
}

.fm-title--grey {
  background-color: #d3d3d3;
  border-color: @grey-border-color;
  color: @grey-text-color;
  padding: 10px 10px 10px 10px;
}

.fm-title--navy {
  background-color: @navy-background-color;
  border-color: @navy-border-color;
  color: @navy-text-color;
}