
.fm-footer {
  width: 100%;
  padding: 0.9em 1.1em;
  color: @text-color;
  background-color: @footer-background-color;
}

.fm-footer__cancel-btn {
  .btn-mixin();
  .btn-color-mixin(@footer-btn-text-color, @footer-btn-background-color, @footer-btn-border-color);
  .font-mixin();
  float: left;
}

.fm-footer__action-btn {
  .btn-mixin();
  .btn-color-mixin(@footer-btn-text-color, @footer-btn-background-color, @footer-btn-border-color);
  .font-mixin();
  float: right;
}

