.btn-mixin() {
  display: inline-block;
  padding: 6px 12px;
  margin-bottom: 0;
  font-weight: 400;
  text-align: center;
  white-space: nowrap;
  vertical-align: middle;
  touch-action: manipulation;
  cursor: pointer;
  user-select: none;
}

.btn-color-mixin(@text-color, @background-color, @border-color) {
  color: @text-color;
  background-image: none;
  background-color: @background-color;
  border: 1px solid @border-color;
  border-radius: 2px; // example of prefixes
}

.font-mixin() {
  font-family: Arial;
  font-size: 14px;
  line-height: 1.2;
}

.h3-form-mixin() {
  font-family: Arial;
  font-size: 16px;
  font-weight: bold;
  line-height: 1.2;
  color: #007FBB;
}