
.quote-details {
  width: 100%;
  padding: 0.5em;
  color: @text-color;
  background-color: @title-background-color;
}

.quote-details__body {

}

.quote-details__body__header {
  color: @text-color;
}

.quote-details__panels {
  margin: 20px 0 0 0;
}

.quote-details__title__button {
  .btn-mixin();
  .btn-color-mixin(@footer-btn-text-color, @footer-btn-background-color, @footer-btn-border-color);
  .font-mixin();
}

.quote-details__title__content-body {
  border: solid 1px lightgray;
  margin-bottom: 20px
}

.quote-details__form-row {
  margin-bottom: 10px;
}

.quote-details__h3 {
  .h3-form-mixin()
}