@blue-text-color: #FFF;
@blue-background-color: #45B0E6;
@blue-border-color: #3398CA;

@orange-text-color: #FFF;
@orange-background-color: #F38230;
@orange-border-color: #DE772C;

@grey-text-color: #545454;
@grey-background-color: #F9F9F9;
@grey-border-color: #BCBCBC;

@white-text-color: #808080;
@white-background-color: #FFF;
@white-border-color: #808080;

.fm-button {
  .btn-mixin();
}

.fm-button--blue {
  .btn-color-mixin(@blue-text-color, @blue-background-color, @blue-border-color);
}

.fm-button--orange {
  .btn-color-mixin(@orange-text-color, @orange-background-color, @orange-border-color)
}

.fm-button--grey {
  .btn-color-mixin(@grey-text-color, @grey-background-color, @grey-border-color)
}

.fm-button--white {
  .btn-color-mixin(@white-text-color, @white-background-color, @white-border-color)
}

.fm-button--small {
  width: 3em;
}

.fm-button--medium {
  width: 5em;
}

.fm-button--large {
  width: 8em;
}

.fm-button--full {
  width: 100%;
}